import React, { useState } from "react";

// í—¤ë” íƒ€ì…ë³„ ìƒ‰ìƒ ì •ì˜
const headerColors = {
  AH: "#ff4d4d", // Red
  PH_PRESENTATION: "#ff66cc", // Pink (Presentation ê³„ì¸µ PH)
  PH_SESSION: "#a64dff", // Purple (Session ê³„ì¸µ PH)
  TH: "#4da6ff", // Blue
  NH: "#00cc99", // Green
  DH: "#ffcc00", // Yellow
  BIT: "#4a4a4a", // Dark Gray
  DATA: "#000000", // Black
};

// íŒ¨í‚·ë³„ ìƒì„¸ ì •ë³´ ì •ì˜
const packetInfo = {
  AH: "Application Header \n ì‚¬ìš©ìì™€ ë„¤íŠ¸ì›Œí¬ ê°„ì˜ ì¸í„°í˜ì´ìŠ¤ë¥¼\n ì œê³µí•˜ë©° ë°ì´í„°ë¥¼ ìƒì„±.",
  PH_PRESENTATION: "Presentation Header \n ë°ì´í„°ë¥¼ íŠ¹ì • í˜•ì‹ìœ¼ë¡œ ë³€í™˜í•˜ê±°ë‚˜\n ì•”í˜¸í™”/ë³µí˜¸í™”ë¥¼ ìˆ˜í–‰.",
  PH_SESSION: "Session Header \n í†µì‹  ì„¸ì…˜ì„ ì„¤ì •, ê´€ë¦¬, ì¢…ë£Œí•˜ëŠ” ë°\n í•„ìš”í•œ ì •ë³´ë¥¼ í¬í•¨.",
  TH: "Transport Header \n ë°ì´í„°ì˜ ì‹ ë¢°ì„±ì„ ë³´ì¥í•˜ê³ ,\n íŒ¨í‚· ì¬ì „ì†¡ ë° ìˆœì„œ ì œì–´ë¥¼ ë‹´ë‹¹.",
  NH: "Network Header \n ë°ì´í„°ì˜ ê²½ë¡œë¥¼ ì„¤ì •í•˜ê³  ë¼ìš°íŒ…ì„ ìˆ˜í–‰.",
  DH: "Data Link Header \n ë…¸ë“œ ê°„ ë°ì´í„° ì „ì†¡ê³¼ ì—ëŸ¬ ê°ì§€ë¥¼ ë‹´ë‹¹.",
  BIT: "Binary Data \n ë¬¼ë¦¬ ê³„ì¸µì„ í†µí•´ ì „ì†¡ë˜ëŠ”\n ë¹„íŠ¸ ë‹¨ìœ„ ë°ì´í„°.",
  DATA: "Payload Data \n ìƒìœ„ ê³„ì¸µì—ì„œ ìƒì„±ëœ ìµœì¢… ë°ì´í„°ë¡œ,\n ì „ì†¡í•´ì•¼ í•  í•µì‹¬ ë°ì´í„° ë‚´ìš©.",
};

const BuildUpPacket = ({ highlightedLayer }) => {
  const [selectedPacket, setSelectedPacket] = useState(null);

  const packetData = {
    application: ["AH", "DATA"],
    presentation: ["PH_PRESENTATION", "AH", "DATA"],
    session: ["PH_SESSION", "PH_PRESENTATION", "AH", "DATA"],
    transport: ["TH", "PH_SESSION", "PH_PRESENTATION", "AH", "DATA"],
    network: ["NH", "TH", "PH_SESSION", "PH_PRESENTATION", "AH", "DATA"],
    dataLink: ["DH", "NH", "TH", "PH_SESSION", "PH_PRESENTATION", "AH", "DATA", "DH"],
    physical: ["BIT"],
  };

  // íŒ¨í‚· í´ë¦­ ì‹œ ì •ë³´ í‘œì‹œ
  const handlePacketClick = (packet) => {
    setSelectedPacket(packet);
  };

  const containerStyle = {
    textAlign: "center",
    padding: "20px",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    gap: "15px",
  };

  const rowStyle = (isHighlighted) => ({
    display: "flex",
    justifyContent: "center",
    gap: "10px",
    backgroundColor: isHighlighted ? "lightblue" : "white",
    padding: "5px",
    borderRadius: "4px",
    transition: "background-color 0.3s ease",
  });

  const boxStyle = (packet) => ({
    border: "2px solid black",
    padding: "8px 12px",
    fontSize: "14px",
    fontWeight: "bold",
    whiteSpace: "nowrap",
    cursor: "pointer",
    borderRadius: "5px",
    backgroundColor: headerColors[packet] || "gray",
    color: packet === "DATA" ? "white" : "black",
    transition: "border-color 0.3s ease",
    animation: highlightedLayer ? "pulse 1.5s infinite" : "none",
  });

  const bitStyle = {
    ...boxStyle("BIT"),
    width: "400px", // ê°€ë¡œ ê¸¸ì´ ì¦ê°€
    padding: "12px 20px", // íŒ¨ë”© ì¡°ì •
    fontSize: "16px", // ê¸€ê¼´ í¬ê¸° ì¦ê°€
    textAlign: "center", // í…ìŠ¤íŠ¸ ì •ë ¬
  };

  const highlightPacket = (layer, packet) =>
    highlightedLayer === layer && packetData[layer]?.includes(packet);

  return (
    <div style={containerStyle}>
      <h2>Build-Up Packet</h2>
      {Object.entries(packetData).map(([layer, packets], index) => (
        <div key={index} style={rowStyle(layer === highlightedLayer)}>
          {packets.map((packet, idx) => (
            <div
              key={idx}
              style={{
                ...(packet === "BIT" ? bitStyle : boxStyle(packet)),
                borderColor: highlightPacket(layer, packet) ? "orange" : "black",
              }}
              onClick={() => handlePacketClick(packet)}
            >
              {/* UIì—ì„œëŠ” ëª¨ë“  PHë¥¼ ë™ì¼í•˜ê²Œ "PH"ë¡œ í‘œì‹œ */}
              {packet.startsWith("PH_") ? "PH" : packet}
            </div>
          ))}
        </div>
      ))}

      {/* íŒ¨í‚· ì •ë³´ íŒì—… */}
      {selectedPacket && (
        <div
          style={{
            position: "fixed",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            backgroundColor: "white",
            padding: "20px",
            borderRadius: "10px",
            boxShadow: "0 4px 8px rgba(0, 0, 0, 0.2)",
            width: "300px",
            textAlign: "center",
            zIndex: 1000,
          }}
        >
          <h3>Packet Info</h3>
          <p style={{ whiteSpace: "pre-line" }}>
            <strong>
              {selectedPacket.startsWith("PH_") ? "PH" : selectedPacket}
            </strong>
          </p>
          <p style={{ whiteSpace: "pre-line" }}>
            {packetInfo[selectedPacket] ||
              "No additional information available."}
          </p>
          <button
            onClick={() => setSelectedPacket(null)}
            style={{
              padding: "10px 15px",
              backgroundColor: "#e63946",
              color: "white",
              border: "none",
              borderRadius: "6px",
              fontSize: "14px",
              cursor: "pointer",
              fontWeight: "bold",
              transition: "background-color 0.3s ease",
            }}
            onMouseOver={(e) => (e.target.style.backgroundColor = "#c92a2a")}
            onMouseOut={(e) => (e.target.style.backgroundColor = "#e63946")}
          >
            ë‹«ê¸°
          </button>
        </div>
      )}
    </div>
  );
};

export default BuildUpPacket;
